$blue: #0A2B4E;
$yellow: #FDB827;
$white: #fff;

.page-template-page-about {
  .aboutIntroInner {
    text-align: center;
  }
  #researchPark {
    background: $blue;
    color: $white;
    padding-top: 0; padding-bottom: 0;
    min-height: 400px;
    h2 {
      color: $white;
    }
    .headingSpacer {
      background-color: $yellow;
    }
    .aboutIntroSliderContainer {
      height: 400px;
      .aboutIntroSlider {
        position: absolute;
        top: 0; right: 0; left: 0; bottom: 0;
        overflow: hidden;
        .aboutIntroSliderItem {
          height: 400px;
          background-position: center;
          background-size: cover;
        }
      }
    }
  }
  #nonProfit {
    background-color: $blue;
    background-position: center;
    background-size: cover;
    .nonProfitInner {
      padding-top: 6rem;
      padding-bottom: 6rem;
      color: #fff;
      .headingSpacer {
        background-color: $yellow;
      }
    }
  }
  #ulpCommunity {
    .headingSpacer {
      margin-left: 0;
    }
    h2 {
      text-align: left;
      span {
        color: $yellow;
      }
    }
    p {
      text-align: left;
    }
    a.memberLink {
      color: #000;
      &:hover, &:visited {
        color: #000;
        text-decoration: none;
      }
    }
    .commMemberContainer {
      margin-bottom: 2rem;
      h4 {
        font-size: 1rem;
        font-weight: bold;
      }
      .memberTitle {
        text-transform: uppercase;
        font-size: 0.8rem;
      }
      .commMemberImgWrap {
        background-size: cover;
        background-position: center;
        width: 100%;
        height: 250px;
        display: inline-block;
        margin: auto auto 1rem auto;
        &:hover {
        }
      }
    }
  }
}

.single-members {
  .memberContainer {
    margin-top: 2rem;
    margin-bottom: 2rem;
  }
  .memberName {
    margin-bottom: 2rem;
  }
  .memberInfoList {
    li {
      text-transform: uppercase;
      font-size: 0.8rem;
      letter-spacing: 0.01rem;
      margin-bottom: 10px;
      span {
        color: #ddd;
      }
    }
  }
}